button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: 1px solid;
	border-color: black;
	border-radius: 3px;
	background: transparent;
	color: rgba(0, 0, 0, .8);
	@include font-size(1);
	line-height: 1;
	padding: .5em 1em;

	&:hover {
		border-color: $color__border-button-hover;
	}

	&:active,
	&:focus {
		border-color: white;
		color: black;
	}
}
